<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="5">
          <div>
            <el-input
              placeholder=""
              v-model="queryInfo.specimenCode"
              class="input-with-select"
              @change="getSpecimenList"
            ></el-input>
          </div>
        </el-col>
        <el-col :span="4">
          <el-select
            v-model="queryInfo.specimenTypeId"
            placeholder=""
            @visible-change="$forceUpdate()"
          >
            <el-option key label="" value></el-option>
            <el-option
              v-for="item in specimenTypeList"
              :key="item.dictId"
              :label="item.dictValue"
              :value="item.dictId"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-select
            v-model="queryInfo.specimenStatusId"
            placeholder=""
            @visible-change="$forceUpdate()"
          >
            <el-option key label="" value></el-option>
            <el-option key="0" label="" value="0"></el-option>
            <el-option key="1" label="" value="1"></el-option>
          </el-select>
        </el-col>
        <el-col :span="4">
          <el-select
            v-model="queryInfo.testTypeId"
            placeholder=""
            @visible-change="$forceUpdate()"
          >
            <el-option
              v-for="item in testTypeList"
              :key="item.dictId"
              :label="item.dictValue"
              :value="item.dictId"
            ></el-option>
          </el-select>
        </el-col>
        <el-col
          :span="4"
          v-show="(((rightList || {}).specimenManagement || {}).reportAction||{}).batchCreateNegativeReport"
        >
          <el-button
            type="success"
            :disabled="!batchCreateNegativeReportBtnVisable"
            @click="showBatchCreateNegativeReportDialog"
          ></el-button>
        </el-col>
      </el-row>
      <br />
      <el-row :gutter="20">
        <el-col :span="7">
          <div>
            <center class="date-title"></center>
            <el-date-picker
              v-model="queryInfo.createTimeRange"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              unlink-panels
              format="yyyyMMdd"
              value-format="yyyyMMdd"
              @blur="$forceUpdate()"
            ></el-date-picker>
          </div>
        </el-col>
        <el-col :span="7">
          <div class="block">
            <center class="date-title"></center>
            <el-date-picker
              v-model="queryInfo.recvTimeRange"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              unlink-panels
              format="yyyyMMdd"
              value-format="yyyyMMdd"
              @blur="$forceUpdate()"
            ></el-date-picker>
          </div>
        </el-col>
        <el-col :span="7">
          <div class="block">
            <center class="date-title"></center>
            <el-date-picker
              v-model="queryInfo.reportTimeRange"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              unlink-panels
              format="yyyyMMdd"
              value-format="yyyyMMdd"
              @blur="$forceUpdate()"
              :disabled="queryInfo.specimenStatusId!=1"
            ></el-date-picker>
          </div>
        </el-col>
      </el-row>
      <br />
      <el-row :gutter="20">
        <el-col :span="4">
          <div>
            <el-select
              v-model="queryInfo.hosId"
              placeholder=""
              @change="getDepartmentListByHosId(queryInfo.hosId)"
              @visible-change="$forceUpdate()"
              filterable
              remote
              :remote-method="getHospitalList"
            >
              <el-option key="0" label="" value="0"></el-option>
              <el-option
                v-for="item in hospitalList"
                :key="item.hosId"
                :label="item.hosName"
                :value="item.hosId"
              ></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="4">
          <div>
            <el-select
              v-model="queryInfo.deptId"
              placeholder=""
              @visible-change="$forceUpdate()"
              filterable
            >
              <el-option key label="" value></el-option>
              <el-option
                v-for="item in departmentList"
                :key="item.deptId"
                :label="item.deptName"
                :value="item.deptId"
              ></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="4">
          <div>
            <el-select v-model="queryInfo.sortRule" placeholder="">
              <el-option key="0" label="" value="0"></el-option>
              <el-option key="1" label="" value="1"></el-option>
              <el-option key="2" label="" value="2"></el-option>
              <el-option key="3" label="" value="3"></el-option>
              <el-option key="4" label="" value="4"></el-option>
              <el-option key="5" label="" value="5"></el-option>
              <el-option key="6" label="" value="6"></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="4">
          <div>
            <el-input
              placeholder=""
              v-model="queryInfo.patientName"
              class="input-with-select"
              @change="getSpecimenList"
            >
              <el-button slot="append" icon="el-icon-search" @click="getSpecimenList"></el-button>
            </el-input>
          </div>
        </el-col>
        <el-col
          :span="3"
          v-if="rightList.specimenManagement&&(((rightList || {}).specimenManagement || {}).specimenAction||{}).saveSpecimen"
        >
          <div>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </div>
        </el-col>
        <el-col
          :span="5"
          v-if="(((rightList || {}).specimenManagement || {}).specimenAction||{}).bathImportSpecimen"
        >
          <div>
            <el-upload
              class="upload-demo"
              name="uploadFile"
              :action="$http.defaults.baseURL+'specimen/uploadFromFile.do'"
              :on-success="batchImportSuccess"
            >
              <el-button type="success">Excel</el-button>
              <div slot="tip" class="el-upload__tip">excel</div>
            </el-upload>
          </div>
        </el-col>
      </el-row>
      <el-table :data="pageResult.rows" border style="width: 100%" stripe>
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-table :data="[scope.row]" border style="width: 100%" stripe>
              <el-table-column prop="specimenSituation.dictValue" label=""></el-table-column>
              <el-table-column prop="patientAge" label=""></el-table-column>
              <el-table-column prop="patientGender" label=""></el-table-column>
              <el-table-column prop="bedNum" label=""></el-table-column>
              <el-table-column prop="doctor.docName" label=""></el-table-column>
              <el-table-column
                v-if="scope.row.specimenStatusId==1"
                prop="report.specimenResult.dictValue"
                label=""
              ></el-table-column>
              <el-table-column
                v-if="scope.row.specimenStatusId==1"
                prop="report.referenceValue.dictValue"
                label=""
              ></el-table-column>
              <el-table-column prop="report.createTime" label=""></el-table-column>
              <el-table-column
                v-if="scope.row.specimenStatusId==1"
                prop="report.reporter.managerName"
                label=""
              ></el-table-column>
              <el-table-column
                v-if="scope.row.specimenStatusId==1"
                prop="report.reviewer.managerName"
                label=""
              ></el-table-column>
            </el-table>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#" width="43"></el-table-column>
        <el-table-column prop="specimenCode" label="" sortable></el-table-column>
        <el-table-column prop="specimenType.dictValue" label=""></el-table-column>
        <el-table-column prop="testType.dictValue" label="" sortable></el-table-column>
        <el-table-column prop="patientName" label=""></el-table-column>
        <el-table-column prop="patientAge" label=""></el-table-column>
        <el-table-column prop="patientTellphone" label="" width="120"></el-table-column>
        <el-table-column prop="patientGender" label="" width="43"></el-table-column>
        <el-table-column prop="specimenStatusId" label="">
          <template slot-scope="scope">
            <span v-if="scope.row.specimenStatusId==1">
              <el-tag type="success"></el-tag>
            </span>
            <span v-if="scope.row.specimenStatusId==0">
              <el-tag></el-tag>
            </span>
            <span v-if="scope.row.specimenStatusId==2">
              <el-tag type="info"></el-tag>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="hospital.hosName" label="" sortable></el-table-column>
        <el-table-column prop="department.deptName" label=""></el-table-column>
        <el-table-column prop="patientType.dictValue" label=""></el-table-column>
        <el-table-column prop="doorNum" label="" sortable :sort-method="sortByDoorNum"></el-table-column>
        <el-table-column prop="createTime" label="" sortable></el-table-column>
        <el-table-column prop="recvTime" label="" sortable></el-table-column>
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <el-tooltip
              effect="dark"
              :content="scope.row.specimenStatusId==0?'':''"
              placement="top"
              :enterable="false"
            >
              <el-button
                type="success"
                icon="el-icon-document-checked"
                size="mini"
                @click="scope.row.specimenStatusId==0?showAddReportDialog(scope.row.specimenId,scope.row.testTypeId):showEditReportDialog(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteSpecimen(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[5,10,15,20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageResult.total"
      ></el-pagination>
    </el-card>
    <!---->
    <el-dialog title="" :visible.sync="addDialogVisable" @close="addDialogClosed">
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        class="demo-addForm"
      >
        <el-form-item label="" prop="specimenCode">
          <el-input v-model="addForm.specimenCode" ref="specimenCodeRef"></el-input>
        </el-form-item>
        <el-row :gutter="10">
          <el-col :span="8">
            <el-form-item label="" prop="testTypeId">
              <el-select
                v-model="addForm.testTypeId"
                placeholder=""
                @visible-change="$forceUpdate()"
                @change="addFormTestTypeChanged()"
              >
                <el-option
                  v-for="item in testTypeList"
                  :key="item.dictId"
                  :label="item.dictValue"
                  :value="item.dictId"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="specimenTypeId">
              <el-select
                v-model="addForm.specimenTypeId"
                placeholder=""
                @visible-change="$forceUpdate()"
              >
                <el-option
                  v-for="item in specimenTypeList"
                  :key="item.dictId"
                  :label="item.dictValue"
                  :value="item.dictId"
