                  :label="item.dictValue"
                  :value="item.dictId"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="8">
            <el-form-item label="" prop="hosId">
              <el-select
                v-model="addForm.hosId"
                placeholder=""
                @change="getDepartmentListByHosId(addForm.hosId)"
                @visible-change="$forceUpdate()"
                filterable
                remote
                :remote-method="getHospitalList"
              >
                <el-option
                  v-for="item in hospitalList"
