                placeholder=""
                @change="getDoctorListByDeptId(addForm.deptId)"
                @visible-change="$forceUpdate()"
                filterable
              >
                <el-option
                  v-for="item in departmentList"
                  :key="item.deptId"
                  :label="item.deptName"
                  :value="item.deptId"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="docId">
              <el-select v-model="addForm.docId" placeholder="">
                <el-option
                  v-for="item in doctorList"
                  :key="item.docId"
                  :label="item.docName"
                  :value="item.docId"
                  @visible-change="$forceUpdate()"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="8">
            <el-form-item label="" prop="doorNum">
              <el-input v-model="addForm.doorNum"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="bedNum">
              <el-input v-model="addForm.bedNum"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="patientTypeId">
              <el-select v-model="addForm.patientTypeId" placeholder="">
                <el-option
                  v-for="item in patientTypeList"
                  :key="item.dictId"
                  :label="item.dictValue"
                  :value="item.dictId"
                  @visible-change="$forceUpdate()"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="8">
            <el-form-item label="" prop="patientName">
              <el-input v-model="addForm.patientName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="patientAge">
              <el-input v-model="addForm.patientAge"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="patientGender">
              <el-select
                v-model="addForm.patientGender"
                placeholder=""
                @visible-change="$forceUpdate()"
              >
                <el-option key="" label="" value=""></el-option>
                <el-option key="" label="" value=""></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="12">
            <el-form-item label="" prop="patientTellphone">
              <el-input v-model="addForm.patientTellphone"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="10">
          <el-col :span="11">
            <el-form-item label="" prop="recvTime">
              <el-date-picker
                v-model="addForm.recvTime"
                type="datetime"
                placeholder=""
                format="yyyyMMdd HH:mm"
                value-format="yyyyMMdd HH:mm"
                @blur="$forceUpdate()"
              ></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col :span="11">
            <el-form-item label="" prop="createTime">
              <el-date-picker
                v-model="addForm.createTime"
                type="datetime"
                placeholder=""
                format="yyyyMMdd HH:mm"
                value-format="yyyyMMdd HH:mm"
                @blur="$forceUpdate()"
              ></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisable = false"> </el-button>
        <el-button type="primary" @click="addSpecimen"> </el-button>
      </span>
    </el-dialog>
    <!---->
