<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="5">
          <el-select v-model="queryInfo.userStatus" placeholder="" @change="getUserList">
            <el-option key="1" label="" value="1"></el-option>
            <el-option key="2" label="" value="2"></el-option>
            <el-option key="0" label="" value="0"></el-option>
          </el-select>
        </el-col>
        <el-col :span="5">
          <div class="grid-content bg-purple">
            <el-input
              placeholder=""
              v-model="queryInfo.name"
              class="input-with-select"
              @change="getUserList"
            >
              <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
            </el-input>
          </div>
        </el-col>
        <el-col :span="6" v-if="(((rightList || {}).userManagement || {}).userAction||{}).saveUser">
          <div class="grid-content bg-purple">
            <el-button type="primary" @click="showAddDialog"></el-button>
          </div>
        </el-col>
      </el-row>
      <el-table :data="pageResult.rows" border style="width: 100%" stripe>
        <el-table-column type="index" label="#" width="50"></el-table-column>
        <el-table-column prop="loginName" label=""></el-table-column>
        <el-table-column prop="loginPasswd" label=""></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="role.roleName" label=""></el-table-column>
        <el-table-column prop="telephone" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="registerTime" label=""></el-table-column>
        <el-table-column prop="department" label=""></el-table-column>
        <el-table-column prop="post" label=""></el-table-column>
        <el-table-column
          label=""
          width="200"
          v-if="(((rightList || {}).userManagement || {}).userAction||{}).updateUser||(((rightList || {}).userManagement || {}).userAction||{}).deleteUser"
        >
          <template
            slot-scope="scope"
            v-if="(((rightList || {}).userManagement || {}).userAction||{}).updateUser||(((rightList || {}).userManagement || {}).userAction||{}).deleteUser"
          >
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row.userId)"
                v-if="(((rightList || {}).userManagement || {}).userAction||{}).updateUser"
              ></el-button>
            </el-tooltip>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteUser(scope.row.userId)"
              v-if="(((rightList || {}).userManagement || {}).userAction||{}).deleteUser&&scope.row.userStatus==1"
            ></el-button>
            <el-button
              type="success"
              icon="el-icon-upload2"
              size="mini"
              @click="cancelDeleteUser(scope.row.userId)"
              v-if="(((rightList || {}).userManagement || {}).userAction||{}).deleteUser&&scope.row.userStatus==2"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[5,10,15,20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageResult.total"
      ></el-pagination>
      <!---->
      <el-dialog title="" :visible.sync="addDialogVisable" width="40%" @close="addDialogClosed">
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="100px"
          class="demo-addForm"
        >
          <el-row :gutter="0">
            <el-col :span="12">
              <el-form-item label="" prop="loginName">
                <el-input v-model="addForm.loginName"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="loginPasswd">
                <el-input v-model="addForm.loginPasswd"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="0">
            <el-col :span="12">
              <el-form-item label="" prop="name">
                <el-input v-model="addForm.name"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="roleId">
                <el-select
                  v-model="addForm.roleId"
                  placeholder=""
                  @visible-change="$forceUpdate()"
                  filterable
                >
                  <el-option
                    v-for="item in roleList"
                    :key="item.roleId"
                    :label="item.roleName"
                    :value="item.roleId"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="0">
            <el-col :span="12">
              <el-form-item label="" prop="department">
                <el-input v-model="addForm.department"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="post">
                <el-input v-model="addForm.post"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="0">
            <el-col :span="12">
              <el-form-item label="" prop="telephone">
                <el-input v-model="addForm.telephone"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="email">
                <el-input v-model="addForm.email"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisable = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </el-dialog>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editDialogVisable"
      width="40%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
        class="demo-addForm"
      >
        <el-row :gutter="0">
          <el-col :span="12">
            <el-form-item label="" prop="loginName">
              <el-input v-model="editForm.loginName"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="loginPasswd">
              <el-input v-model="editForm.loginPasswd"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="0">
          <el-col :span="12">
            <el-form-item label="" prop="name">
              <el-input v-model="editForm.name"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="roleId">
              <el-select
                v-model="editForm.roleId"
                placeholder=""
                @visible-change="$forceUpdate()"
                filterable
              >
                <el-option
                  v-for="item in roleList"
                  :key="item.roleId"
                  :label="item.roleName"
                  :value="item.roleId"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="0">
          <el-col :span="12">
            <el-form-item label="" prop="department">
              <el-input v-model="editForm.department"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="post">
              <el-input v-model="editForm.post"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="0">
          <el-col :span="12">
            <el-form-item label="" prop="telephone">
              <el-input v-model="editForm.telephone"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="" prop="email">
              <el-input v-model="editForm.email"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisable = false"> </el-button>
        <el-button type="primary" @click="updateUser"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pageResult: {
        total: 0,
        rows: []
      },
      queryInfo: {
        pageNum: 1,
        pageSize: 5,
        hosName: ''
      },
      addDialogVisable: false,
      // 
      addForm: {
        hosName: '',
        hosAddress: ''
      },
      // 
      addFormRules: {
        loginName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 16, message: ' 4  16 ', trigger: 'blur' }
        ],
        loginPasswd: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleId: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editDialogVisable: false,
      editForm: {
        hosId: 0,
        hosName: '',
        hosAddress: ''
      },
      // 
      editFormRules: {
        loginName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 16, message: ' 4  16 ', trigger: 'blur' }
        ],
        loginPasswd: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleId: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      rightList: {},
      roleList: []
    }
  },
  created() {
    // 
    this.getRight().then(data => {
      // 
      this.queryInfo.userStatus = '1'
      this.getUserList()
    })
  },
  methods: {
    // pageSize
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getUserList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getUserList()
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          // 
          const { data: checkLoginNameExist } = await this.$http.post(
            'user/checkLoginNameExist.do?loginName=' + this.addForm.loginName
          )
          if (checkLoginNameExist) {
            return this.$message.error('')
          }
          const { data: result } = await this.$http.post(
            'user/save.do',
            this.addForm
          )
          this.addDialogVisable = false
          if (result.success) {
            this.getUserList()
            return this.$message({
              message: '',
              type: 'success'
            })
          }
          return this.$message.error('')
        }
      })
    },
    // 
    async showEditDialog(userId) {
      this.getAllRole().then(async data => {
        const { data: user } = await this.$http.post(
          'user/getByUserId.do?userId=' + userId
        )
        this.editForm = user
        this.editDialogVisable = true
      })
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    async getUserList() {
      if (
        (((this.rightList || {}).userManagement || {}).userAction || {})
          .listUser === undefined
      ) {
        return this.$message.error('')
      }
      const { data: result } = await this.$http.post(
        'user/list.do?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize,
        { name: this.queryInfo.name, userStatus: this.queryInfo.userStatus }
      )
      this.pageResult = result
    },
    // 
    async updateUser() {
      this.$refs.editFormRef.validate(async valid => {
        if (valid) {
          const { data: result } = await this.$http.post(
            'user/update.do',
            this.editForm
          )
          this.editDialogVisable = false
          if (result.success) {
            this.getUserList()
            return this.$message({
              message: '',
              type: 'success'
            })
          }
          return this.$message.error('')
        }
      })
    },
    // id
    async deleteUser(userId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { data: result } = await this.$http.post(
            'user/delete.do?userId=' + userId
          )
          if (result.success) {
            this.getUserList()
            return this.$message({
              type: 'success',
              message: result.message
            })
          }
          return this.$message.error(result.message)
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    async getRight() {
      // 
      const loginName = window.sessionStorage.getItem('loginName')
      const { data: rightList } = await this.$http.post(
        'right/listByLoginName.do?loginName=' + loginName
      )
      this.rightList = rightList
    },
    async getAllRole() {
      const { data: roleList } = await this.$http.post('role/listAll.do', {
        roleStatus: 1
      })
      this.roleList = roleList
    },
    showAddDialog() {
      this.getAllRole().then(data => {
        this.addDialogVisable = true
      })
    },
    async cancelDeleteUser(userId) {
      const { data: result } = await this.$http.post(
        'user/cancelDelete.do?userId=' + userId
      )
      if (result.success) {
        this.getUserList()
        return this.$message({
          type: 'success',
          message: result.message
        })
      }
      return this.$message.error(result.message)
    }
  }
}
</script>
<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #ccc;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
