<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="5">
          <div class="grid-content bg-purple">
            <el-select
              v-model="queryInfo.hosId"
              placeholder=""
              @change="searchHospitalSelectChanged"
              filterable
              remote
              :remote-method="getHospitalList"
            >
              <el-option key="0" label="" value="0"></el-option>
              <el-option
                v-for="item in hospitalList"
                :key="item.hosId"
                :label="item.hosName"
                :value="item.hosId"
              ></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="5">
          <div class="grid-content bg-purple">
            <el-input placeholder="" v-model="queryInfo.deptName" class="input-with-select">
              <el-button slot="append" icon="el-icon-search" @click="getDepartmentList"></el-button>
            </el-input>
          </div>
        </el-col>
        <el-col
          :span="6"
          v-if="(((rightList || {}).hospitalManagement || {}).departmentAction||{}).saveDepartment"
        >
          <div class="grid-content bg-purple">
            <el-button type="primary" @click="showAddDialog"></el-button>
          </div>
        </el-col>
      </el-row>
      <el-table :data="pageResult.rows" border style="width: 100%" stripe>
        <el-table-column type="index" label="#" width="150"></el-table-column>
        <el-table-column prop="hospital.hosName" label=""></el-table-column>
        <el-table-column prop="deptName" label=""></el-table-column>
        <el-table-column
          label=""
          width="200"
          v-if="(((rightList || {}).hospitalManagement || {}).departmentAction||{}).deleteDepartment||(((rightList || {}).hospitalManagement || {}).departmentAction||{}).updateDepartment"
        >
          <template slot-scope="scope">
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialog(scope.row.deptId)"
                v-if="(((rightList || {}).hospitalManagement || {}).departmentAction||{}).updateDepartment"
              ></el-button>
            </el-tooltip>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteDepartment(scope.row.deptId)"
              v-if="(((rightList || {}).hospitalManagement || {}).departmentAction||{}).deleteDepartment"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pageNum"
        :page-sizes="[5, 10,15,20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageResult.total"
      ></el-pagination>
      <!---->
      <el-dialog title="" :visible.sync="addDialogVisable" width="40%" @close="addDialogClosed">
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addFormRef"
          label-width="100px"
          class="demo-addForm"
        >
          <el-form-item label="">
            <el-select v-model="addForm.hosId" placeholder="">
              <el-option
                v-for="item in hospitalList"
                :key="item.hosId"
                :label="item.hosName"
                :value="item.hosId"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="deptName">
            <el-input v-model="addForm.deptName"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDialogVisable = false"> </el-button>
          <el-button type="primary" @click="addDepartment"> </el-button>
        </span>
      </el-dialog>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editDialogVisable"
      width="40%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
        class="demo-addForm"
      >
        <el-form-item label="">
          <el-select v-model="editForm.hospital.hosId" placeholder="">
            <el-option key="0" label="" value="0"></el-option>
            <el-option
              v-for="item in hospitalList"
              :key="item.hosId"
              :label="item.hosName"
              :value="item.hosId"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="deptName">
          <el-input v-model="editForm.deptName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisable = false"> </el-button>
        <el-button type="primary" @click="updateDepartment"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      pageResult: { total: 0, rows: [] },
      hospitalList: [{ hosId: '' }],
      queryInfo: {
        deptName: '',
        pageNum: 1,
        pageSize: 5,
        hosId: ''
      },
      addDialogVisable: false,
      // 
      addForm: {
        hosId: '',
        deptId: '',
        deptName: ''
      },
      // 
      addFormRules: {
        deptName: [{ required: true, message: '', trigger: 'blur' }]
      },
      editDialogVisable: false,
      editForm: {
        hospital: { hosId: '', hosName: '', hosAddress: '' },
        deptId: '',
        deptName: ''
      },
      // 
      editFormRules: {
        deptName: [{ required: true, message: '', trigger: 'blur' }]
      },
      rightList: {}
    }
  },
  created() {
    // 
    this.getRightList().then(data => {
      this.getDepartmentList()
      this.getHospitalList()
    })
  },
  methods: {
    // pageSize
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getDepartmentList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getDepartmentList()
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addDepartment() {
      this.$refs.addFormRef.validate(async valid => {
        if (valid) {
          // 
          const { data: result } = await this.$http.post(
            'department/save.do',
            this.addForm
          )
          this.addDialogVisable = false
          if (result.success) {
            this.getDepartmentList()
            return this.$message({
              message: '',
              type: 'success'
            })
          }
          return this.$message.error('')
        }
      })
    },
    // 
    async showEditDialog(deptId) {
      // deptId
      const { data: result } = await this.$http.post(
        'department/getById.do?deptId=' + deptId
      )
      // 
      this.getHospitalList()
      this.editForm = result
      console.log(this.editForm)
      this.editDialogVisable = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 1.
    async getHospitalList(hospitalName) {
      const { data: hospitalList } = await this.$http.post(
        'hospital/listAll.do',
        {
          hosName: hospitalName
        }
      )
      this.hospitalList = hospitalList
    },
    async showAddDialog() {
      this.getHospitalList()
      if (this.queryInfo.hosId !== '' && this.queryInfo.hosId !== '0') {
        console.log(this.queryInfo.hosId)
        this.addForm.hosId = this.queryInfo.hosId
      } else {
        this.addForm.hosId = this.hospitalList[0].hosId
      }
      this.addDialogVisable = true
    },
    async getDepartmentList() {
      if (
        !(
          ((this.rightList || {}).hospitalManagement || {}).departmentAction ||
          {}
        ).selectDepartment
      ) {
        return this.$message.error('')
      }
      const {
        data: result
      } = await this.$http.post(
        'department/list.do?pageNum=' +
          this.queryInfo.pageNum +
          '&pageSize=' +
          this.queryInfo.pageSize,
        { deptName: this.queryInfo.deptName, hosId: this.queryInfo.hosId }
      )
      this.pageResult = result
      console.log(this.pageResult)
    },
    async updateDepartment() {
      this.$refs.editFormRef.validate(async valid => {
        if (valid) {
          const { data: result } = await this.$http.post(
            'department/update.do',
            this.editForm
          )
          this.editDialogVisable = false
          if (result.success) {
            this.getDepartmentList()
            return this.$message({
              message: '',
              type: 'success'
            })
          }
          return this.$message.error('')
        }
      })
    },
    deleteDepartment(deptId) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(async () => {
          const { data: result } = await this.$http.post(
            'department/delete.do?deptId=' + deptId
          )
          if (result.success) {
            this.getDepartmentList()
            return this.$message({
              type: 'success',
              message: '!'
            })
          }
          return this.$message.error('')
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
    },
    searchHospitalSelectChanged() {
      this.getDepartmentList()
    },
    async getRightList() {
      // 
      const loginName = window.sessionStorage.getItem('loginName')
      const { data: rightList } = await this.$http.post(
        'right/listByLoginName.do?loginName=' + loginName
      )
      this.rightList = rightList
    }
  }
}
</script>
<style lang="less" scoped>
</style>
